/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
body {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 12px;
	vertical-align: baseline;
}
html, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
a {
    color: #000; 
    text-decoration: none; 
    outline: none
}
a:hover, a:active {
    text-decoration: none; 
    color:#000;
}
button {
	border:none;
	cursor:pointer;
}
button:active,
button:focus {
	outline:none;
}
@charset "utf-8";
body {
    background-color:#f5f7f7;
    font-family: Arial, Helvetica, sans-serif;
}
body header {
    height:32px;
    line-height:33px;
    background-color:#fff;
    border-bottom:1px solid #d9e6e8;
}
#container,
header .wrap {
    width:1100px;
    margin:0 auto;
}
#container {
    position: relative;
}

/* header */
header .wrap * {
    display:inline-block;
}
header h1 {
    display:inline-block;
    padding: 0 19px;
    border-width: 0 1px;
    border-style: solid;
    border-color: #d9e6e8;
    font-weight:bold;
    color:#bbb;
}
header .go-store {
    color:#18292e;
}
header .go-store span {
    display: inline-block;
    width: 16px;
    height: 14px;
    margin:0 3px 0 10px;
    line-height: 16px;
    border-radius: 3px;
    text-align: center;
    background-color: #3fdcdb;
    color: #fff;
    font-size: 10px;
}
header .go-store > i {
    padding-left:5px;
    font-size:11px;
}
header .header-menu {
    float:right;
}
header .header-menu li {
    margin:0 5px;
    color:#bbb;
    font-size:11px;
}
header .header-menu li:nth-child(2) {
    color:red;
}
header .alarm {
    position: relative;
    float:right;
    padding:0 8px;
    margin-left:15px;
    border-width:0 1px;
    border-style:solid;
    border-color:#d9e6e8;
    font-size:11px;
}
header .alarm button {
    background-color:transparent;
    font-weight:bold;
    color:#3a5965;
}
header .alarm span {
    position: absolute;
    bottom:-10px;
    right:12px;
    width:23px;
    height:18px;
    border-radius:30px;
    text-align:center;
    line-height:20px;
    color:#fff;
    font-weight:bold;
    font-size:11px;
    background:-webkit-gradient(linear, left top, left bottom, from(#3fdcdb), to(#4ab0e3));
    background:linear-gradient(#3fdcdb, #4ab0e3);
}

/* side-bar */
.side-bar {
    position: absolute;
    left:0;
}
.side-bar nav,
.side-bar .shop-profile,
.side-bar .shop-album {
    width:160px;
}
.side-bar nav {
    padding:15px 0;
    border:1px solid #d9e6e8;
    background-color:#fff;
}
.side-bar nav ul {
    padding-left:20px;
}
.side-bar nav li {
    line-height:20px;
    font-size:13px;
}
.side-bar nav li.list-active {
    color:#3fd2d1;
}
.side-bar nav li:hover {
    color:#3fd2d1;
    cursor: pointer;
}
.side-bar nav li.list-header {
    margin-top:20px;
    font-size:15px;
    font-weight:bold;
}
.side-bar .shop-profile {
    padding-top:35px;
    background:-webkit-gradient(linear, left top, right top, from(#3fdcdb), to(#4ab0e3));
    background:linear-gradient(to right, #3fdcdb, #4ab0e3);
    color:#fff;
}
.side-bar .shop-profile .shop-name {
    padding:0 20px;
    color:#fff;
    font-size:20px;
    font-weight:bold;
}
.side-bar strong {
    margin-right:8px;
}
.side-bar .manager {
    margin-top:35px;
    padding:0 20px 10px;
    border-bottom:1px solid rgba(0, 0, 0, 0.1);
    font-size:13px;
}
.side-bar .staff {
    padding:10px 20px;
    font-size:13px;
}
.side-bar .staff strong {
    margin-right:15px;
}
.side-bar .staff span {
    float:right;
    margin-top:5px;
}
.side-bar .staff-list {
    display: inline-block;
}
.side-bar .staff-list li {
    display:inline-block;
    width:24px;
    height:24px;
    margin-left:-8px;
    border-radius: 50%;
    background-color:#ddd;
    border:1px solid #bbb;
    vertical-align: middle;
}
.side-bar .shop-album {
    display: inline-block;
    margin:5px 0;
    padding:15px 20px;
    border:1px solid #d9e6e8;
    background-color:#f9f9fb;
    font-size:13px;
    font-weight:bold;
    color:#718c96;
}
.side-bar .shop-album svg {
    float:right;
    font-size: 15px;
    margin-top: -2px;
}

/* content */
.content {
    margin-left:190px;
    padding-top:30px;
}
.content h2 {
    margin-bottom:45px;
    color:#37454a;
    font-size:25px;
    font-weight:bold;
}
.content .view-filter li {
    display:inline-block;
    margin-right:20px;
    padding-bottom:3px;
    font-size:15px;
    font-weight: bold;
    color:#bad0d3;
    cursor: pointer;
}
.content .view-filter .filter-active {
    border-bottom:3px solid #3fd2d1;
    color:#37454a;
}
.content .add-new {
    margin:30px 0 15px;
    color:#37454a;
    font-size:25px;
    text-align:center;
}
.content .add-new button {
    padding:10px 0 10px 53px;
    background:url(data:image/png;base64,) no-repeat 0 5px;
    cursor: pointer;
}

/* 우리샵 앨범 - 앨범 */
.view-album .album {
    position: relative;
    display:inline-block;
    width:195px;
    margin:20px 10px 0px 0;
    font-weight:bold;
    color:#fff;
    cursor:pointer;
}
.view-album .album-photo {
    text-align:center;
    line-height:200px;
}
.view-album .album img {
    max-width:100%;
    max-height:200px;
    vertical-align: middle;
}
.view-album .album-info {
    width:100%;
    height:100px;
    padding:30px 15px 10px;
    background-color:rgba(0, 0, 0, 0.7);
}
.view-album .album .time {
    margin:5px 0 20px;
    color:#a2a1a0;
}
.view-album .album .price-for-member {
    float:right;
    color:#a2a1a0;
}
.view-album .img-cnt {
    position: absolute;
    top:10px;
    right:10px;
    padding-left:15px;
    background:url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAsAAAALCAYAAACprHcmAAAABGdBTUEAALGPC/xhBQAAACBjSFJNAAB6JgAAgIQAAPoAAACA6AAAdTAAAOpgAAA6mAAAF3CculE8AAAABmJLR0QAAAAAAAD5Q7t/AAAACXBIWXMAAAsSAAALEgHS3X78AAAAOUlEQVQY02P8/uv3fwYCgJONlZGBgYGBgZBiZHkmQqYiayJaMUkmDyLFLOjBQxXw/dfv/4zIHEIaAL6FG5VHRMw3AAAAAElFTkSuQmCC) no-repeat;
}

/* 우리샵 앨범 - 이달의 아트 */
.month-list > li {
    margin-bottom:10px;
    padding:20px 15px;
    border:1px solid #ddd;
    background-color:#fff;
}
.month-list > li:first-child {
    margin-top:25px;
}
.month-list p {
    margin-bottom: 30px;
    font-size: 18px;
}
.month-list img {
    max-width:200px;
    max-height:200px;
    margin-right:10px;
}
.month-list .img-list {
    white-space: nowrap;
    overflow-x: auto;
    overflow-y: hidden;
}
.month-list .img-list > li {
    display:inline-block;
}

/* pagenation */
.pagenation {
    font-weight:bold;
    text-align:center;
}
.pagenation svg {
    margin:0 12px;
    color:#3fdcdb;
}
.content .pagenation {
    padding:30px 0;
}

/* modal */
.modal {
    position: fixed;
    top: 0;
    right: 0;
    width:100%;
    height:100vh;
    background-color:rgba(0, 0, 0, 0.3)
}
.modal .modal-content {
    position: absolute;
    left:50%;
    top:120px;
    margin-left:-300px;
    width:600px;
    background-color:#fff;
}
.modal .modal-header {
    padding:15px 10px;
}
.modal .modal-body .img-slide-box {
    position:relative;
    width:340px;
    height:500px;
    margin:73px auto;
}
.modal .modal-header span {
    padding-left:10px;
    color:#3fdcdb;
    font-size:18px;
    font-weight:bold;
}
.modal .btn-close-modal {
    float:right;
    width: 30px;
    height: 30px;
    border: none;
    background:url(data:image/png;base64,) no-repeat;
    text-indent: -99999999px;
}
.modal-footer button {
    float:left;
    width:50%;
    height:50px;
    font-size:16px;
    font-weight: bold;
}
.modal-footer button:first-child {
    background-color:#fff;
    color:#969ea0;
}
.modal-footer button:last-child {
    background-color:#29373b;
    color:#fff;
}

/* 예약현황 위젯 */
.reserve-widget {
    position: fixed;
    top: 55px;
    right: 35px;
    width: 250px;
    border-radius:12px;
    -webkit-box-shadow:0px 2px 10px #d9e6e8;
            box-shadow:0px 2px 10px #d9e6e8;
}
.reserve-widget .widget-header {
